.App-global {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /*font-size: calc(10px + 2vmin);*/
  color: white;
}

.landing-page{
  height: 80vh;
  display:block;
  align-content: center;
  font-size: calc(10px + 2vmin);
}

.left-side-landing {
  margin-right: 50px; /* Add some space between the text and the image */
  float:left;
}

.right-side-landing {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden; /* Hide any overflow if necessary */
  float:right;
  border-radius:50%;
  width: 300px;
  height: 300px;
}
.right-side-landing img{
  max-width: 100%;
  max-height: 100%;
}

.view-more{
  height: 30vh;
  text-align: center;
  font-size: calc(10px + 2vmin);
}

.projects{
  display:tabel-cell;
  flex-direction: column;
}
.project-title{
  font-size: calc(10px + 1vmin);
}

.project-div-left{
  display:block;
  flex-direction:column;
  float:left;
  padding-right:10vw;
  padding:50px;
}
.project-div-right{
  display:block;
  flex-direction:column;
  float:right;
  padding:50px;
}
.project-image{
  float:left;
  width: 200px;
  height:200px;
}
.project-image img{
  width:100%;
  height: 100%;    
}
.project-descript{
  float:right;
  max-width: 30vw;
  padding-left:20px;
}
