
.landing-page{
  height: 80vh;
  display:block;
  align-content: center;
  font-size: calc(10px + 2vmin);
}

.left-side-landing {
  margin-right: 50px; /* Add some space between the text and the image */
  float:left;
}

.right-side-landing {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden; /* Hide any overflow if necessary */
  float:right;
  border-radius:50%;
  width: 300px;
  height: 300px;
}
.right-side-landing img{
  max-width: 100%;
  max-height: 100%;
}
  
.button-container {
  display: flex;
  justify-content: center; /* Center the button */
  width: 100%;
}
  
.continue-button {
  padding: 12px 30px;
  font-size: 16px;
  background-color: #124e66;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 1s ease;
}
  
.continue-button:hover {
  color:#2e3944;
}
  
  