.projects{
    background-color: #282c34;
    min-height: 100vh;
    /*font-size: calc(10px + 2vmin);*/
    color: white;
    flex-direction: column;
    align-content: center;
  }

  .projects-content{
    text-align: center;
    align-content: center;
  }

  .project-title{
    font-size: calc(10px + 1vmin);
  }
  

  
  .project-div-left{
    display:block;
    flex-direction:column;
    float:left;
    padding-right:10vw;
    margin-left:10vw;
    padding:50px;
  }
  .project-div-right{
    display:block;
    flex-direction:column;
    float:right;
    padding:50px;
    padding-right:20vw;
  }
  .project-image{
    float:left;
    width: 200px;
    height:200px;
  }
  .project-image img{
    width:100%;
    height: 100%;    
  }
  .project-descript{
    float:right;
    max-width: 15vw;
    padding-left:20px;
  }
  